.view {
    position: absolute;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0) scale(0.25);
    background: #111;
    overflow: hidden;
    border-radius: 100%;
    opacity: 0;
    transition: 1s opacity linear, 1.5s transform linear;

    &:before {
        content: "";
        display: block;
        width: 105%;
        height: 105%;
        position: absolute;
        margin: 0 auto;
        top: -3%;
        left: -3%;
        background: radial-gradient(ellipse at center, rgba(0,0,0,0.75) 0%,rgba(0,0,0,0.95) 35%,rgba(0,0,0,1) 80%);
        background-size: cover;
        z-index: 14;
        pointer-events: none;
    }

    .loaded & {
        transform: translate3d(-50%, -50%, 0) scale(1);
        opacity: 1;
    }

    .game-over &:after {
        content: "Game Over";
        font-size: 2vw;
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%);
        z-index: 200;
        pointer-events: none;
    }

    .game-over-reset &:after {
        content: "Derp.";
        font-size: 2vw;
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -60%);
        z-index: 200;
        pointer-events: none;
    }

    .game-over-win &:after {
        content: "Wrecked it.";
        font-size: 2vw;
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -60%);
        z-index: 200;
        pointer-events: none;
    }
}

.stage {
    position: absolute;
    background: #eee;
}

.entity,
.tile {
    position: absolute;
    z-index: 1;
}
