.level-select {
    position: absolute;
    height: auto;
    width: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    color: #bbb;

    .animating & {
        transition: 0.4s opacity linear;
    }

    .loaded & {
        opacity: 1;
    }

    .levels {
        overflow: hidden;
        border-color: #444;
        border-style: solid;
        border-width: 3px 2px;
        border-radius: 10px;
        font-size: 5vmin;
    }

    .level {
        cursor: default;
        padding: 2vh;
        border-top: 2px solid #444;
        background-color: rgba(34, 34, 34, 1);
        transition: 0.3s background-color linear;

        &:first-child {
            border: 0;
        }

        &:hover {
            background-color: rgba(110,110,110, 0.5);
        }

        .loaded & {
            cursor: pointer;
        }
    }

    .stats-panel {
        top: 100%;
        margin-top: -4px;
    }
}
