.tile-details,
.tool-select {
  position: fixed;
  top: 0;
  right: 0;
  background: #222;
  color: #eee;
  padding: 20px;

  h4 {
    margin: 20px 0 0;

    &:first-child {
      margin-top: 0;
    }
  }

  div {
    padding: 10px 0 0;
  }

  &.no-details {
    display: none;
  }

  .hidden {
      visibility: hidden;
      height: 0;
      position: absolute;
  }
}

.tile-details {
    min-width: 15%;
}

.tool-select {
    right: auto;
    left: 0;

    div {
        padding: 10px;
    }
}
