.tile-level {
    .tile {
        z-index: 10;
        position: absolute;
        left: 0;
        top: 0;
        cursor: pointer;
        border-width: 1px;
    }

    .tile-1 {
        &.style-1 {
            background: #555;

            &.doorway:before,
            &.doorway:after {
                background: #555;
            }
        }

        &.style-2 {
            background: #222;

            &.doorway:before,
            &.doorway:after {
                background: #222;
            }
        }

        &.style-1.doorway,
        &.style-2.doorway {
            background: #fff;

            &:before,
            &:after {
                display: block;
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
            }

            &.north-south:before {
                width: 28%;
                left: 0;
            }

            &.north-south:after {
                width: 28%;
                right: 0;
            }

            &.east-west:before {
                height: 28%;
                top: 0;
            }

            &.east-west:after {
                height: 28%;
                bottom: 0;
            }
        }
    }

    .tile-2 {
        &.style-1:before {
            background: #ccc;
        }

        &.style-2:before {
            background: #aaa;
        }

        &.switch:after {
            content: '';
            display:block;
            position: absolute;
            width: 50%;
            height: 50%;
            top:50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: red;
            border: 1px solid #666;

            &.flipped {
                background: green;
            }
        }
    }
}
