.player {
    background: red;
    border-radius: 27%;
    border: 2px solid #000;

    &.near-mine:before{
        content: "";
        display: block;
        position: absolute;
        width: 40%;
        height: 40%;
        left: 30%;
        top: 30%;
        background: red;
        border-radius: 100%;
        z-index: 200;
    }

    &:after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        background: #444;
        z-index: 100;
        border-radius: 100%;
        transform: scale(1.14);
        border: 2px dotted #ddd;
    }

    &.shake:after,
    &.shake:before {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) infinite both;
        transform: translate3d(0, 0, 0);
        backface-visibility: hidden;
        perspective: 1000px;
    }
}
