.proximity-mine {
    z-index: 2;

    &:before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        pointer-events: none;
        transform: scale(1);
        transition: 1.4s transform linear, 0.6s opacity linear;
        opacity: 1;

        background-color: maroon;
        border-radius: 5%;
        background: radial-gradient(ellipse at center, rgba(30,52,73,1) 0%,rgba(106,158,201,1) 39%,rgba(122,23,34,1) 51%,rgba(107,142,198,1) 68%,rgba(64,73,89,1) 100%);
    }

    &.attack {
        background-color: red;

        &:before {
            opacity: 0.4;
        }

        &.child,
        &.child:before {
            border-radius: 100%;
            opacity: 0.8;
        }

        &.child:before {
            opacity: 0.6;
        }
    }

    &.mine-mom {
        background-color: lightgreen;

        &:before {
            opacity: 0.3;
        }
    }

    &.mined {
        z-index: 2000;

        &:before {
            background: red;
            transform: scale(10);
            border-radius: 100%;
        }
    }

    &.destroyed {
        opacity: 0;
    }

    &.shake:before {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) infinite both;
        transform: translate3d(0, 0, 0);
        backface-visibility: hidden;
        perspective: 1000px;
    }
}
